<template>
  <!--주문내역 목록-->
  <div class="swiperWrap swiperDivi swipeType03" data-slide-per-view="auto">
    <div class="swiper-container">
      <button type="button" class="swiper-button-prev">이전</button>
      <ul class="swiper-wrapper">
        <li
        v-for="(item, index) in parsedData"
        v-bind:key="index"
        class="swiper-slide">
          <div  >
            <a @click="click('상세정보',item)" :class="isOnConsulting ? 'cb-disabled-button' : ''">
            <div  class="swCont">
              <div class="swFlag">
                <span class="flagHis" v-bind:class="colorF(item)">{{storeName(item)}}</span>
<!--                <span class="flagHis purpleF">인천공항 1터미널점</span>-->
<!--                <span class="flagHis blueF">인천공항 1터미널점</span>-->
<!--                <span class="flagHis tealF">인천공항 1터미널점</span>-->
<!--                <span class="flagHis redF">인천공항 1터미널점</span>-->
              </div>
              <div class="swInfo">
                <span class="date">{{item.ordDt}}</span>
                <span v-if="item.etcGoosCnt > 0" class="tit lineClamp2">{{item.goosNm}} {{ $t("common.외") }} {{item.etcGoosCnt}}{{ $t("common.건") }}</span>
                <span v-else class="tit lineClamp2">{{item.goosNm}}</span>
                <!-- <span class="price"><b>${{item.dollarAmt | numberWithCommas}}</b>{{item.wonAmt | numberWithCommas}}원</span> -->
                <span class="price"><b>${{numberWithCommas(item.dollarAmt)}}</b>{{amount(item)}}</span>
                <span class="data">
                  <span style="padding-top:2px">{{ item.ordNo ? $t("orderInfo.주문번호") : $t("orderInfo.교환권번호") }}</span>
                  <span>{{item.ordNo ? item.ordNo : item.offBillNo}}</span>
                </span>
                <span class="data">
                  <span>{{ $t("orderInfo.출국정보") }}</span>
                  <span>{{item.exitDt | exitDt_str }} {{item.exitTime | exitTime_str}} {{ item.fltCode }}</span>
                </span>
              </div>
            </div>
            </a>
            <div class="swBtns">
              <a v-if="item.bctImgUrl != ''" @click="click('인도장', item)" data-id="mapViewPopup" class="layerPopupButton" aria-haspopup="true">{{ $t("orderInfo.인도장위치") }}</a>
              <a v-if="item.bctImgUrl == ''" style="font-weight:bold; color:#bbbbbb; cursor:default" @click="click('인도장', item)" data-id="mapViewPopup" class="layerPopupButton" aria-haspopup="true">{{ $t("orderInfo.인도장위치") }}</a>

              <!-- <button type="button" data-id="mapViewPopup" class="layerPopupButton" aria-haspopup="true">지도보기</button> -->
              <a v-if="item.ordCnclYn == 'Y'" role="button" @click="click('주문취소',item)" >{{ $t("orderInfo.주문취소") }}</a>
              <a v-if="item.ordCnclYn != 'Y'" style="font-weight:bold; color:#bbbbbb; cursor:default" role="button" @click="click('주문취소 불가',item)" >{{ $t("orderInfo.주문취소불가") }}</a>
            </div>
          </div>
        </li>
        <!--캐러셀 항목 5개 이상일시에만 노출-->
        <li v-if="data.list.length > 5" class="swiper-slide btnMors">
          <button @click="click('더보기')" type="button" class="btMore">
            {{ $t("common.더보기") }}</button>
        </li>
      </ul>
      <button type="button" class="swiper-button-next" >다음</button>
    </div>
  </div>
  <!--//주문내역 목록-->
</template>
<style scoped>
a{
  cursor:pointer;
}
</style>

<script>
import language from '@/utils/getLanguage';
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'OrderDetailsCard',
  props: {
    data: {},
  },
  mixins: [consultingStatus],
  mounted() {
    this.$uiHandler.swiperNextPrevShow();
    this.$uiHandler.popup.popupOn();
  },
  computed: {
    parsedData() {
      if (this.data.list.length > 5) {
        return this.data.list.slice(0, 5);
      }

      return this.data.list;
    },
  },
  filters: {
    exitDt_str(param) {
      const yyyy = param.substring(0, 4);
      const mm = param.substring(4, 6);
      const dd = param.substring(6, 8);
      return `${yyyy }-${ mm }-${ dd}`;
    },
    exitTime_str(param) {
      const hh = param.substring(0, 2);
      const mm = param.substring(2, 4);
      return `${hh }:${ mm}`;
    },
  },
  methods: {
    storeName(item) {
      switch (item.storCd) {
        case '01': // 부산점
          return this.$t('branch_name.부산점');
        case '10': // 인천1
          return this.$t('branch_name.인천1');
        case '05': // 명동
          return this.$t('branch_name.명동점');
        case '07': // 인천2
          return this.$t('branch_name.인천2');
        case '08': // 강남
          return this.$t('branch_name.강남점');
        default: // 온라인
          return this.$t('branch_name.인터넷면세점');
      }
    },
    colorF(item) {
      switch (item.storCd) {
        case '01': // 부산점
          return 'tealF';
        case '10': // 인천1
          return 'redF';
        case '05': // 명동
          return 'purpleF';
        case '07': // 인천2
          return 'greenF';
        case '08': // 강남
          return 'redF';
        default: // 온라인
          return 'brownF';
      }
    },
    amount(item) {
      if (language === this.$dictionary.languageType.CN) {
        if (item.offBillNo) {
          return `${this.numberWithCommas(item.wonAmt)}韩元`;
        }
        return `￥${this.numberWithCommas(item.cnyAmt)}`;
      }

      return `${this.numberWithCommas(item.wonAmt)}원`;
    },
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    },
    click(id, item) {
      switch (id) {
        case '인도장':
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_197);
          this.$store.commit('SHOW_MAP', item.bctImgUrl);
          break;
        case '주문취소':
          // this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_198);
          // window.cbMoveParent(`${this.data.urls.주문취소}${item.ordNo}`);
          break;

        case '상세정보':
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_196);
          window.cbMoveParent(`${this.data.urls.주문상세}${item.ordNo}`);
          break;

        case '더보기':
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_199);
          window.cbMoveParent(this.data.urls.나의주문목록);
          break;

        default:
          break;
      }
    },
  },
};
</script>